const toSI = (lbs) => {
  let ans = lbs / 2.205;

  if (ans < 0.5) {
    ans = Math.round(ans * 10000);
    return ans / 10 + "g";
  }

  ans = Math.round(ans * 100);
  return ans / 100 + "kg";
};

export default function AnimalCard(props) {
  const data = props.data;
  return (
    <div>
      <img src={data.image_link} alt={data.id} width="300px" />
      <h2>{data.name}</h2>
      <table>
        <tbody>
          <tr>
            <th>Latin Name:</th>
            <td>{data.latin_name}</td>
          </tr>

          <tr>
            <th>Animal Type:</th>
            <td>{data.animal_type}</td>
          </tr>

          <tr>
            <th>Active Time:</th>
            <td>{data.active_time}</td>
          </tr>

          <tr>
            <th>Length:</th>
            <td>
              {data.length_min} - {data.length_max} feet
            </td>
          </tr>

          <tr>
            <th>Weight: </th>
            <td>
              {toSI(data.weight_min)} - {toSI(data.weight_max)}
            </td>
          </tr>

          <tr>
            <th>Lifespan: </th>
            <td>{data.lifespan} years</td>
          </tr>
          <tr>
            <th>Habitat:</th>
            <td>{data.habitat}</td>
          </tr>
          <tr>
            <th>Diet: </th>
            <td>{data.diet}</td>
          </tr>
          <tr>
            <th>Location: </th>
            <td>{data.geo_range}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
