import axios from "axios";
import React, { useEffect, useState } from "react";

export default function DictionaryLabel(props) {
  const [wordData, setWordData] = useState(["Looking..."]);

  useEffect(() => {
    axios
      .get("https://api.dictionaryapi.dev/api/v2/entries/en/" + props.word)
      .then((res) => {
        setWordData(
          res.data[0].meanings[0].definitions.map((val) => val.definition)
        );
      })
      .catch((err) => {
        setWordData(["No definition found!"]);
        // console.log(err);
      });
  }, [props.word]);

  return (
    <div>
      {wordData
        ? wordData.map((val, ind) => {
            return <span key={ind}> {val}</span>;
          })
        : ""}
    </div>
  );
}
