import axios from "axios";
import React, { useEffect, useState } from "react";
import AnimalCard from "./components/AnimalCard";
import DictionaryLabel from "./components/DictionaryLabel";
import "./styles.css";

export default function App() {
  const [animalsLi, setAnimalsLi] = useState(null);

  const [word, setWord] = useState("");

  useEffect(() => {
    axios
      .get("https://zoo-animal-api.herokuapp.com/animals/rand/10")
      .then((res) => {
        setAnimalsLi(res.data);
      })
      .catch((err) => {
        setAnimalsLi(null);
        console.log(err);
      });

    document.onselectionchange = () => {
      const text = window.getSelection().toString().trim();

      if (text.match(/[^a-z]/gi) !== null) {
        setWord("");
        return;
      }
      setWord(text);
    };
  }, []);

  return (
    <div>
      <div
        style={{
          position: "fixed",
          left: "7px",
          bottom: "20px",
          color: "white",
          background: "black",
          opacity: "0.8",
          padding: "10px",
          borderRadius: "8px",
          width: "95%",
          fontSize: "1.1rem",
          display: word ? "block" : "none"
        }}
      >
        {word ? <DictionaryLabel word={word} /> : "No definition found!"}
      </div>
      <h1 style={{ textAlign: "center" }}>10 Random Animal Facts</h1>
      {animalsLi === null
        ? "Loading..."
        : animalsLi.length === 0
        ? "Error: Trouble fetching data! "
        : animalsLi.map((val) => {
            return <AnimalCard key={val.id} data={val} />;
          })}
    </div>
  );
}
